        </ul>
      </div>
      <!--end nav -->
    </div>
  </div>
  <div class="imgcontainer">
    <img src="../pictures/ShotCounterFrontPage.png" alt="Logo" class ="logo-img1">
  </div>
  <div class="content">
    <h2>STAT TRACKING</h2>
    <h3>For NXL</h3>
  </div>
  <div class="Teamstuff">
    <h1>Team</h1>
    <ul class="team-list">
      <li v-for="(teamName, index) in teams" :key="index">{{ teamName }}</li>
    </ul>
  </div>
  <div class="EventList">
  <h1>Events</h1>
  <ul class="event-list">
    <li v-for="(event, index) in events" :key="index">
      <strong>{{ event.eventName }}</strong> 
      <span class="event-start-date">Start Date "{{ event.eventStart }}"</span>
      <span class="event-end-date">End Date "{{ event.eventEnd }}"</span>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      teams: [],
      events: []
    };
  },
  mounted() {
  // Fetch teams
  fetch("http://localhost:8080/team")
    .then((response) => response.json())
    .then((data) => {  
      this.teams = data.map((item) => item.team);
    });

  // Fetch events
  fetch("http://localhost:8080/events")  
    .then((response) => response.json())
    .then((data) => {
      this.events = data.sort((a, b) => new Date(b.eventStart) - new Date(a.eventStart));
    });
}
};
</script>

